@import '../../styles/_variables';
@import '../../styles/_mixins';

.card-editor__comment-wrapper {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
}

.card-editor__comment {
  width: 100%;
}

.card-editor__comment-header {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.card-editor__time-elapsed {
  font-size: 11px;
}

.card-editor__full-name {
  font-size: 14px;
}

.card-editor__comment-body {
  font-size: 14px;
}

.card-editor__likes {
  .feather {
    width: 18px;
    height: 18px;
  }
}

.card-editor__avatar {
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin-right: 8px;
}
